 /*Custom Checkbox*/
 .hs-input[type=checkbox] {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
   flex-shrink: 0;
   position: relative;
   width: 1rem !important;
   height: 1rem !important;
   margin-top: .25rem !important;
   margin-right: 0.5rem !important;
   min-height: auto !important;
   border: 1px solid var(--border-color--border-secondary) !important;
   border-radius: 0.25rem !important;
   accent-color: var(--border-color--border-secondary) !important;
   transition: background-color 100ms ease, border-color 100ms ease;
 }
 
 .hs-input[type=checkbox]:pressed {
   border-color: var(--border-color--border-secondary) !important;
   accent-color: var(--border-color--border-secondary) !important;
   box-shadow: none;
 }
 
 .hs-input[type=checkbox]:focus {
   border-color: var(--border-color--border-secondary) !important;
   accent-color: var(--border-color--border-secondary) !important;
 }
 
 .hs-input[type=checkbox]:hover {
   border-color: var(--border-color--border-secondary) !important;
   accent-color: var(--border-color--border-secondary) !important;
   cursor: pointer !important;
 }
 
 .hs-input[type=checkbox]:checked {
   border-color: var(--_base--brand--cobalt) !important;
   accent-color: var(--_base--brand--cobalt) !important;
   background-color: var(--_base--brand--cobalt) !important;
 }

 .hs-input[type="checkbox"]:checked::after {
    content: '';
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    background-image: url('data:image/svg+xml,<svg width="16" height="17" viewBox="0 0 16 17" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M5.84166 10.6635L2.99862 7.82045L1.77783 9.04124L5.84166 13.1051L14.2223 4.72445L13.0015 3.50366L5.84166 10.6635Z" fill="white"/></svg>');
    background-position: 50% 50%;
    background-size: 85%;
    background-repeat: no-repeat;
  }

 
